.list-header {
  padding: 0 16px 0 5px;
  color: #c4c5d6;
}

.item-row-equipments {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: nowrap;

  .label {
    font-family: "Ubuntu", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;

    padding: 16px 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    &:first-child {
      width: 15%;
    }

    &:nth-child(2) {
      width: 20%;
    }

    &:nth-child(3) {
      width: 20%;
    }

    &:nth-child(4) {
      width: 14%;
    }

    &:nth-child(5) {
      width: 12%;
    }

    &:nth-child(6) {
      width: 19%;
    }
  }
}

.item-row-vehicles {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: nowrap;

  .label {
    font-family: "Ubuntu", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;

    padding: 16px 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    &:first-child {
      width: 15%;
    }

    &:nth-child(2) {
      width: 9%;
    }

    &:nth-child(3) {
      width: 10%;
    }

    &:nth-child(4) {
      width: 20%;
    }

    &:nth-child(5) {
      width: 15%;
    }

    &:nth-child(6) {
      width: 15%;
    }
  }
}