.modal-header-wrapp {
  box-sizing: border-box;
  height: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // padding: 16px;
  overflow: hidden;
  .left-part {
    width: 50%;
    padding: 16px;
    box-sizing: border-box; 
    h5 {
      font-family: Ubuntu;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
    }
  }
  .right-part {
    border-left: 0.5px solid #dbdce7;
    width: 50%;
    display: flex;
    padding: 16px;
    box-sizing: border-box;
  }
}
