.ItemDetils-wrapp {
  padding: 0 16px 0 5px;

  .ItemDetils {
    color: #444444;
    min-height: 64px;
    background: #ffffff;
    box-shadow: 0px 2px 10px rgba(22, 84, 152, 0.1);
    border-radius: 4px;
    margin-bottom: 8px;
    border-left: 4px solid transparent;

    .icon {
      img {
        max-width: 100%;
        height: auto;
      }
    }

    &.item-added {
      border-left: 4px solid #165498;
      color: #c4c5d6;
    }

    &.item-row-vehicles {
      display: flex;
      justify-content: space-around;
      flex-direction: row;
      flex-wrap: nowrap;

      .label {
        font-family: "Ubuntu", sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 13px;
        line-height: 15px;
        padding: 8px 4px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        &:first-child {
          width: 15%;
        }

        &:nth-child(2) {
          width: 9%;
        }

        &:nth-child(3) {
          width: 10%;
        }

        &:nth-child(4) {
          width: 20%;
        }

        &:nth-child(5) {
          width: 15%;
        }

        &:nth-child(6) {
          width: 15%;
        }

        &.add-btn {
          padding-right: 12px;
          align-items: flex-end;
        }

        &.delete-btn {
          display: flex;
          align-items: center;
          justify-content: space-around;
          flex-direction: row;
        }
      }
    }

    &.item-row-equipments {
      display: flex;
      justify-content: space-around;
      flex-direction: row;
      flex-wrap: nowrap;

      .label {
        font-family: "Ubuntu", sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 13px;
        line-height: 15px;
        padding: 8px 4px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        &:first-child {
          width: 15%;
        }

        &:nth-child(2) {
          width: 20%;
        }

        &:nth-child(3) {
          width: 20%;
        }

        &:nth-child(4) {
          width: 14%;
        }

        &:nth-child(5) {
          width: 12%;
        }

        &:nth-child(6) {
          width: 19%;
        }

        &.add-btn {
          padding-right: 12px;
          align-items: flex-end;
        }

        &.delete-btn {
          display: flex;
          align-items: center;
          justify-content: space-around;
          flex-direction: row;
        }
      }
    }
  }
}