.btn {
    height: 40px;
   
    border: 1px solid;
    box-sizing: border-box;
    box-shadow: 0px 4px 10px rgba(20, 88, 156, 0.1);
    border-radius: 2px;
    padding: 0 70px;
    font-family: Ubuntu;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;    
    margin-left: 4px;
    transition: oll 0.2s;
    &.btn-default {
       
        background: #14589c;
        border: 1px solid #14589c;      
        color: #ffffff;
        
      }
      &:hover{
          opacity: 0.9;
      }
}